.header {
    position: fixed;
    z-index: 10;
    width: 100%;
    background-color: $header-bg;
    .logo {
        float: left;
        margin: 1em;
        a {
            display: block;
            background-image: url(../images/logo_w.png);
            background-image: url(../images/logo_w.svg);
            width: 120px;
            height: 24px;
            background-size: contain;
            text-decoration: none;
            @include hide-text;
        }
    }
    .toggle-menu {
        float: right;
        color: $hamburger-color;
        margin-right: 1em;
        font-size: 24px;
        height: 56px;
        line-height: 56px;
    }
    @include pad{
        .toggle-menu {
            display: none;
            float: left;
        }
    }
}

.header .menu {
    max-height: 0px;
    overflow: hidden;
    transition: max-height 1.5s;
    position: absolute;
    z-index: 5;
    top: 56px;
    left: 0;
    right: 0;
    background: $header-bg;
    a {
        display: block;
        padding: 1em ;
        text-decoration: none;
        color: $link-color;
        &:active { 
            background-color: $menuhover-color;
        }
    }
    @include pad {
        float: right;    
        max-height: none;
        position: static;
        background: initial;
        li {
            float: left;
            margin-right: 1em;
        }
        a {
            padding: 0 1em ;
            height: 56px;
            line-height: 56px;
            border-bottom: 2px solid transparent;
            &:hover {
                background-color: transparent;
                border-bottom: 2px solid $menuhover-color;
            }
        }
    }
}

.menu-show .menu {
    max-height: 200px;
}