.footer {
    padding: 50px 15px;
    background: $footer-bg;
    color: $footer-color;
    .footer-logo {
        text-align: right;
    }
    h3 {
        font-size: $font-h3;
    }
    .about p {
        padding: 15px 15px 15px 0;
    }
    .contact li {
        padding: 12px 12px 12px 5px;
        list-style: disc inside;
        a {
            text-decoration: none; 
            &:hover {
                color: $footer-color;
            }
        }
    }
    .communities {
        padding-top: 15px;
        ul {
            float: right;
        }
        li {
            float: left;
            margin: 10px 0px 0 20px;
        }
        .icon-google,
        .icon-twitter,
        .icon-fb {
            font-size: $font-icon;
        }
        .icon-google{
           color: $icon-google;      
        }
        .icon-twitter{
            color: $icon-twitter;      
        }
        .icon-fb{
            color: $icon-fb;      
        }
    }
    @include pad {
        .contact li {
            padding: 5px;
        }
        .about ,
        .contact,
        .communities {
            float: left;
            margin: 1.5%;
            width: 30.3333%;
        }
        .communities {
            float: right;
            padding-top: 0px;
        }
    }
}
 
 