.banner {
    background-image: url(../images/header_mobile.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 420px;
    padding-top: 56px;
    .description {
        position: relative;
        top: 50%;
        left: 50%;
        width: 300px;
        height: 190px;
        transform: translate(-50%,-50%);
        background-color: $banner-bg;
        color: $banner-color;
        text-align: center;
    }
    h2 {
        font-size: $font-h2;
        padding: 15px 5px;
        span {
            display: block;
        }
    }
    .hex-bigburger {
        display: block;
        padding-top: 5px;
    }
    @include iphone5-landscape {
        .description {
            width: 350px;
        }
    }
    @include pad {
        background-image: url(../images/header_desktop.png);
        .description {
            width: 460px;
            height: 150px;
        }
        h2 span {
            display: inline;
        }
    }
    
}